.exhibit {
  position: relative;
  cursor: pointer;
  height: 100%;
}
.exhibit .playhead.roll-visualizer {
  background-color: orange;
  -webkit-animation: trigger-finger 0.6s;
          animation: trigger-finger 0.6s;
  z-index: 1;
}
.exhibit .playhead {
  z-index: 0;
  position: absolute;
  top: 0px;
  width: 12.5vw;
  height: 100%;
  background-color: #ffffff63;
}
.exhibit .row {
  display: -ms-flexbox;
  display: flex;
  padding-left: 15px;
}
.exhibit .row .triangle:active, .exhibit .row .triangle:active.triggered {
  -webkit-animation: flipper linear 0.2s 0s infinite;
          animation: flipper linear 0.2s 0s infinite;
  z-index: 10000000;
}
.exhibit .row .triangle.old-trigger.track-0 {
  border-bottom: 12vw solid purple;
}
.exhibit .row .triangle.old-trigger.track-1 {
  border-bottom: 12vw solid orange;
}
.exhibit .row .triangle.old-trigger.track-2 {
  border-bottom: 12vw solid green;
}
.exhibit .row .triangle.old-trigger.track-3 {
  border-bottom: 12vw solid white;
}
.exhibit .row .triangle.old-trigger.track-4 {
  border-bottom: 12vw solid gray;
}
.exhibit .row .triangle.old-trigger.track-5 {
  border-bottom: 12vw solid silver;
}
.exhibit .row .triangle.old-trigger.track-6 {
  border-bottom: 12vw solid purple;
}
.exhibit .row .triangle.old-trigger.track-7 {
  border-bottom: 12vw solid orange;
}
.exhibit .row .triangle.old-trigger.track-8 {
  border-bottom: 12vw solid green;
}
.exhibit .row .triangle.old-trigger.track-9 {
  border-bottom: 12vw solid white;
}
.exhibit .row .triangle.old-trigger.track-10 {
  border-bottom: 12vw solid gray;
}
.exhibit .row .triangle.old-trigger.track-11 {
  border-bottom: 12vw solid silver;
}
.exhibit .row .triangle.old-trigger.track-12 {
  border-bottom: 12vw solid purple;
}
.exhibit .row .triangle.old-trigger.track-13 {
  border-bottom: 12vw solid orange;
}
.exhibit .row .triangle.triggered {
  -webkit-animation: trigger-finger 0.6s;
          animation: trigger-finger 0.6s;
  border-bottom: 12vw solid white;
}
.exhibit .row .triangle {
  z-index: 2;
  width: 0;
  height: 0;
  border-bottom: 12vw solid black;
  border-right: 12.5vw solid transparent;
}

@-webkit-keyframes anti-trigger-finger {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.4;
  }
  to {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 1;
  }
}

@keyframes anti-trigger-finger {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.4;
  }
  to {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 1;
  }
}
@-webkit-keyframes trigger-finger {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.4;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@keyframes trigger-finger {
  from {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.4;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes flipper {
  from {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
    opacity: 0.4;
  }
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    opacity: 1;
  }
}
@keyframes flipper {
  from {
    -webkit-transform: rotate(0turn);
            transform: rotate(0turn);
    opacity: 0.4;
  }
  to {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    opacity: 1;
  }
}
