.exhibit {
    position: relative;
    cursor: pointer;
    height: 100%;
    .playhead.roll-visualizer {
        background-color: orange;
        animation: trigger-finger 0.6s;
        z-index: 1;
    }

    .playhead {
        z-index: 0;
        position: absolute;
        top: 0px;
        width: 12.5vw;
        height: 100%;
        //background-color: #ff2e2ea6;
        //background-color: #ff2e2e26;
        background-color: #ffffff63;
    }
    .row {
        display: flex;
        padding-left: 15px;

        .triangle:active, .triangle:active.triggered {
            animation: flipper linear 0.2s 0s infinite;
            z-index: 10000000;
        }

        .triangle.old-trigger.track-0 {
            border-bottom: 12vw solid purple;
        }
        .triangle.old-trigger.track-1 {
            border-bottom: 12vw solid orange;
        }
        .triangle.old-trigger.track-2 {
            border-bottom: 12vw solid green;
        }
        .triangle.old-trigger.track-3 {
            border-bottom: 12vw solid white;
        }
        .triangle.old-trigger.track-4 {
            border-bottom: 12vw solid gray;
        }
        .triangle.old-trigger.track-5 {
            border-bottom: 12vw solid silver;
        }
        .triangle.old-trigger.track-6 {
            border-bottom: 12vw solid purple;
        }
        .triangle.old-trigger.track-7 {
            border-bottom: 12vw solid orange;
        }
        .triangle.old-trigger.track-8 {
            border-bottom: 12vw solid green;
        }
        .triangle.old-trigger.track-9 {
            border-bottom: 12vw solid white;
        }
        .triangle.old-trigger.track-10 {
            border-bottom: 12vw solid gray;
        }
        .triangle.old-trigger.track-11 {
            border-bottom: 12vw solid silver;
        }
        .triangle.old-trigger.track-12 {
            border-bottom: 12vw solid purple;
        }
        .triangle.old-trigger.track-13 {
            border-bottom: 12vw solid orange;
        }
        .triangle.old-trigger {
        }

        .triangle.triggered {
            animation: trigger-finger 0.6s;
            border-bottom: 12vw solid white;
        }

        .triangle {
            z-index: 2;
            width: 0;
            height: 0;
            border-bottom: 12vw solid black;
            border-right: 12.5vw solid transparent;
        }
    }
}

@keyframes anti-trigger-finger {
    from {
        //transform: rotate(0.5turn);
        transform: scale(1);
        opacity: 0.4;
    }
    to {
        //transform: rotate(0turn);
        transform: scale(0.5);
        opacity: 1;
    }
}

@keyframes trigger-finger {
    from {
        //transform: rotate(0.5turn);
        transform: scale(1.5);
        opacity: 0.4;
    }
    to {
        //transform: rotate(0turn);
        transform: scale(1);
        opacity: 1;
    }
}

 @keyframes flipper {
    from {
        transform: rotate(0turn);
        opacity: 0.4;
    }
    to {
        transform: rotate(1turn);
        opacity: 1;
    }
}
 

